.popup {
  display: flex;
  visibility: hidden;
  opacity: 0;
  transition-duration: 0.6s;
  z-index: -10;
  width: 100%;
  height: 100%;
}

.popup::before {
  content: '';
  background: #000000;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  z-index: 4;
}