.popup__title {
  margin: 36px auto 50px 36px;
  font-size: 24px;
  line-height: 1;
  font-weight: 900;
  overflow: hidden;
  text-overflow: ellipsis;
  word-wrap: break-word;
}

@media screen and (max-width: 620px) {

  .popup__title {
    font-size: 18px;
    margin: 28px 22px 75px 22px;
  }
}